/**
 * @usedBy PoModalPasswordRecoveryComponent
 *
 * @description
 *
 * *Enum* para especificao do tipo de recuperao de senha.
 */
export enum PoModalPasswordRecoveryType {
  /** Possibilita ao usurio optar por envio via email ou SMS */
  All = 'all',

  /** Definio para recuperao apenas por email */
  Email = 'email',

  /** Definio para recuperao apenas por SMS */
  SMS = 'sms'
}
