import { AbstractControl } from '@angular/forms';
import { Input, Directive, OnDestroy } from '@angular/core';

import { PoBreadcrumb } from '@po-ui/ng-components';

import { PoJobSchedulerInternal } from './interfaces/po-job-scheduler-internal.interface';
import { PoPageJobSchedulerInternal } from './po-page-job-scheduler-internal';
import { PoPageJobSchedulerService } from './po-page-job-scheduler.service';
import { Subscription } from 'rxjs';

/**
 * @description
 *
 * O `po-page-job-scheduler`  uma pgina para criao e atualizao de agendamentos da execuo de processos (Job Scheduler),
 * como por exemplo: a gerao da folha de pagamento dos funcionrios.
 *
 * Para utilizar esta pgina, basta informar o servio (endpoint) para consumo,
 * sem a necessidade de criar componentes e tratamentos dos dados.
 *
 * Veja mais sobre os padres utilizados nas requisies no [Guia de implementao de APIs](guides/api).
 */
@Directive()
export class PoPageJobSchedulerBaseComponent implements OnDestroy {
  private _subscription = new Subscription();

  /** Objeto com as propriedades do breadcrumb. */
  @Input('p-breadcrumb') breadcrumb?: PoBreadcrumb = { items: [] };

  /**
   * Endpoint usado pelo componente para busca dos processos e parmetros que sero utilizados para criao e edio dos agendamentos.
   *
   * #### Processos
   *
   * Os processos so as tarefas que estaro disponveis para o usurio poder fazer os agendamentos.
   * Ao inicializar o componente, ser feito uma requisio `GET` para o endpoint `{service-api}/processes`, para buscar
   * essa lista de processos.
   *
   * Este endpoint `{service-api}/processes` deve retornar uma lista de objetos que seguem a definio de dados abaixo:
   *
   * ```
   * GET {service-api}/processes
   * ```
   *
   * ```
   * {
   *   items: [
   *     { "processID": "ac4f", "description": "Gerar folha de pagamento" },
   *     { "processID": "df6l", "description": "Relatrio de imposto a recolher" },
   *     { "processID": "dk3p", "description": "Ttulos em aberto" },
   *   ]
   * }
   * ```
   *
   * Desta forma ser renderizado um componente para selecionar o processo e/ou filtr-los.
   *
   * Para realizar o filtro de busca do processo, ser feita uma requisio enviando o contedo digitado na busca atravs do
   * parmetro `search`. Da seguinte forma:
   *
   * ```
   * GET {service-api}/processes?search=relatorio
   * ```
   *
   * > Veja mais sobre paginao e filtros no [Guia de implementao de APIs](guides/api).
   *
   * Tambm  possvel fazer um agendamento de um processo especfico, sem que seja necessrio um endpoint para busca desses
   * processos. Ento, caso o endpoint `{service-api}/processes` no seja vlido, ser apresentado um campo de entrada de
   * texto para o usurio informar diretamente
   * o **identificador do processo - `processID`** e ao salvar ser enviado um `POST` para o endpoint difinido `serviceApi` conforme abaixo:
   *
   * ```
   * POST {service-api}
   * ```
   *
   * *Request payload* - estrutura de dados enviada no corpo da requisio conforme interface `PoJobScheduler`:
   *
   * ```
   * {
   *   "daily": { "hour": 10, "minute": 12 },
   *   "firstExecution": "2018-12-07T00:00:01-00:00",
   *   "recurrent": true,
   *   "processID": "ac0405"
   *   ...
   * }
   * ```
   *
   * Caso seja necessrio informar parmetros e adicionar configuraes no processo selecionado, ser realizado um `GET`
   * como exemplificado abaixo. Os parmetros devem retornar uma lista de objetos que seguem a interface
   * [PoDynamicFormField](/documentation/po-dynamic-form).
   *
   * ```
   * GET {service-api}/processes/:id/parameters
   * ...
   * {
   *   items: [
   *     { "property": "vencimento", type: "date" },
   *     { "property": "imposto-retido", "label": "Imposto Retido", type: "boolean" }
   *   ]
   * }
   * ```
   *
   * #### Salvar e Atualizar
   *
   * Para salvar o agendamento, ser feita uma requisio de criao, passando os valores preenchidos pelo usurio via *payload*.
   * Abaixo uma requisio `POST` disparada, onde as propriedades do *Job Scheduler* foram preenchidas:
   *
   * ```
   *  POST {service-api}
   * ```
   *
   * *Request payload* - estrutura de dados enviada no corpo da requisio conforme interface `PoJobScheduler`:
   *
   * ```
   * {
   *   "firstExecution": "2018-12-07T00:00:01-00:00",
   *   "recurrent": true,
   *   "daily": { "hour": 10, "minute": 12 },
   *   "processID": "ac0405"
   * }
   * ```
   *
   * Caso queira que o componente carregue um agendamento j existente, deve ser includo um parmetro na rota chamado `id`.
   *
   * Exemplo de configurao de rota:
   *
   * ```
   *  RouterModule.forRoot([
   *    ...
   *    { path: 'edit/:id', component: ExampleJobSchedulerComponent },
   *    ...
   *  ],
   * ```
   *
   * Baseado nisso, na inicializao do template ser disparado uma requisio para buscar o recurso que ser editado.
   *
   * ```
   * GET {service-api}/{id}
   * ```
   *
   * Ao atualizar o agendamento, ser disparado um `PUT` com os dados preenchidos.
   * Veja abaixo uma requisio `PUT` disparada, onde a propriedade *recurrent* e *daily* foram atualizadas:
   *
   * ```
   *  PUT {service-api}/{id}
   * ```
   *
   * *Request payload* - estrutura de dados enviada no corpo da requisio conforme interface `PoJobScheduler`:
   *
   * ```
   * {
   *   "firstExecution": "2018-12-07T00:00:01-00:00",
   *   "recurrent": false,
   *   "daily": { "hour": 11, "minute": 30 },
   *   "processID": "ac0405"
   * }
   * ```
   */
  @Input('p-service-api') serviceApi: string;

  /** Ttulo da pgina. */
  @Input('p-title') title: string;

  model: PoJobSchedulerInternal = new PoPageJobSchedulerInternal();

  constructor(protected poPageJobSchedulerService: PoPageJobSchedulerService) {}

  ngOnDestroy() {
    this._subscription.unsubscribe();
  }

  protected loadData(id: string | number) {
    if (!id) {
      this.model = new PoPageJobSchedulerInternal();
      return;
    }

    this._subscription.add(
      this.poPageJobSchedulerService.getResource(id).subscribe(
        (response: PoJobSchedulerInternal) => {
          this.model = response;
        },
        () => {
          this.model = new PoPageJobSchedulerInternal();
        }
      )
    );
  }

  protected markAsDirtyInvalidControls(controls: { [key: string]: AbstractControl }) {
    for (const key in controls) {
      if (controls.hasOwnProperty(key)) {
        const control = controls[key];

        if (control.invalid) {
          control.markAsDirty();
        }
      }
    }
  }
}
