/**
 * @usedBy PoPageBlockedUserComponent
 *
 * @description
 *
 * *Enum* para os tipos de motivo de bloqueio de usurio. As informaes modificam conforme o motivo selecionado pelo desenvolvedor.
 */
export enum PoPageBlockedUserReason {
  /** Sem definio; a tela exibir contedo de bloqueio genrico. */
  None = 'none',

  /** Definio para tentativas de acesso esgotadas. */
  ExceededAttempts = 'exceededAttempts',

  /** Definio para senha expirada. */
  ExpiredPassword = 'expiredPassword'
}
