import { Input, Directive } from '@angular/core';

import { PoPageBlockedUserReason } from './enums/po-page-blocked-user-reason.enum';
import { PoPageBlockedUserReasonParams } from './interfaces/po-page-blocked-user-reason-params.interface';

const PoPageBlockedUserReasonDefault: PoPageBlockedUserReason = PoPageBlockedUserReason.None;
const PoPageBlockedUserParamsDefault: PoPageBlockedUserReasonParams = { attempts: 5, days: 90, hours: 24 };

/**
 * @description
 *
 * O componente `po-page-blocked-user`  utilizado como template para tela de bloqueio de usurio.
 *  possvel definir entre trs tipos de telas para alertar o usurio sobre um eventual bloqueio de login.
 *
 * Cada modelo de bloqueio possui uma imagem e texto adequados  situao.
 * Os textos das telas so pr-definidos e imutveis, porm,
 *  possvel estipular parmetros como dias, horas e tentativas de acesso esgotadas.
 *
 * Por fim, h propriedades para adio de telefone e/ou email para contato e tambm a definio para a url de retorno.
 *
 * Para que as imagens sejam exibidas corretamente,  necessrio incluir o caminho delas ao projeto. Para isso, edite
 * o *assets* no arquivo **angular.json** da aplicao na seguinte ordem:
 * ```
 *   "assets": [
 *     "src/assets",
 *     "src/favicon.ico",
 *     {
 *       "glob": "**\/*",
 *       "input": "node_modules/@po-ui/style/images",
 *       "output": "assets/images"
 *     }
 *   ]
 * ```
 *
 * _______________
 *
 * #### Praticidade
 * O `po-page-blocked-user`, assim como suas propriedades, pode tambm ser transmitido diretamente pelas configuraos de rota e,
 * desta maneira, dispensa-se qualquer meno e/ou importao no restante da aplicao. O exemplo abaixo exemplifica
 * a forma dinmica com a qual o template pode ser gerado se navegasse para uma rota denominada como `/access-denied`:
 *
 *
 * ```
 *   import { PoPageBlockedUserComponent, PoPageBlockedUserReason } from '@po-ui/ng-templates';
 *
 *   ...
 *   const routes: Routes = [
 *     {
 *       path: 'access-denied', component: PoPageBlockedUserComponent, data: {
 *         contactEmail: 'dev.po@po-ui.com',
 *         contactPhone: '0800 1234 000',
 *         reason: PoPageBlockedUserReason.ExpiredPassword,
 *         urlBack: '/home'
 *       }
 *     }
 *     ...
 *   ];
 *
 *   @NgModule({
 *     imports: [RouterModule.forRoot(routes)],
 *     exports: [RouterModule]
 *   })
 *   export class AppRoutingModule { }
 * ```
 *
 * >  essencial que siga a nomenclatura dos atributos exemplificados acima para sua efetiva funcionalidade.
 *
 */
@Directive()
export class PoPageBlockedUserBaseComponent {
  private _params: PoPageBlockedUserReasonParams = { ...PoPageBlockedUserParamsDefault };
  private _reason: PoPageBlockedUserReason = PoPageBlockedUserReason.None;
  private _urlBack: string = '/';

  /**
   * @optional
   *
   * @description
   *
   * Valor para o email de contato que deve ser exibido. A ao est de acordo com o protocolo MAILTO e  possvel definir
   * tanto rotas internas quanto externas.
   */
  @Input('p-contact-email') contactEmail: string;

  /**
   * @optional
   *
   * @description
   *
   * Valor para o telefone de contato que deve ser exibido. A ao est de acordo com o protocolo TEL.
   *
   * > A propriedade no contem tratamento de mscara, fica a critrio do desenvolvedor defini-la.
   */
  @Input('p-contact-phone') contactPhone: string;

  /**
   * @optional
   *
   * @description
   *
   * Designao de valores usados para a customizao da mensagem de bloqueio.
   * Confira abaixo os valores pr-definidos.
   *
   * ```
   *  const customLiterals: PoPageBlockedUserReasonParams = {
   *    attempts: 5,
   *    days: 90,
   *    hours: 24
   *  };
   * ```
   *
   * > Salientamos a importncia e ateno para configurao desses valores conforme definidos no projeto.
   *
   * > Veja os parmetros customizveis na interface `PoPageBlockedUserReasonParams`.
   *
   */
  @Input('p-params') set params(value: PoPageBlockedUserReasonParams) {
    if (value instanceof Object) {
      const keys = Object.keys(value);
      const newParams = { ...PoPageBlockedUserParamsDefault };

      keys.forEach(key => {
        newParams[key] = value[key];
      });

      this._params = newParams;
    } else {
      this._params = { ...PoPageBlockedUserParamsDefault };
    }
  }

  get params() {
    return this._params;
  }

  /** Caminho para a logomarca localizada na parte superior, caso no seja definida ou seja invlida assume a logo padro do PO UI. */
  @Input('p-logo') logo?: string;

  /**
   * @optional
   *
   * @description
   *
   * Definio de motivo de bloqueio de usurio. As informaes modificam conforme o motivo selecionado.
   *
   * > Veja os valores vlidos no *enum* `PoPageBlockedUserReason`.
   *
   * @default `PoPageBlockedUserReason.None`
   */
  @Input('p-reason') set reason(value: PoPageBlockedUserReason) {
    this._reason = (<any>Object).values(PoPageBlockedUserReason).includes(value)
      ? value
      : PoPageBlockedUserReasonDefault;
  }

  get reason() {
    return this._reason;
  }

  /**
   * @optional
   *
   * @description
   *
   * Caminho para a logomarca localizada no rodap.
   */
  @Input('p-secondary-logo') secondaryLogo?: string;

  /**
   * @optional
   *
   * @description
   *
   * URL para a ao de retorno da pgina.
   *
   * @default `/`
   */
  @Input('p-url-back') set urlBack(url: string) {
    this._urlBack = url;
  }

  get urlBack() {
    return this._urlBack;
  }
}
