import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { PoModalAction } from '@po-ui/ng-components';

import { convertToBoolean, isExternalLink, isTypeof } from '../../utils/util';

import { PoPageChangePasswordRecovery } from './interfaces/po-page-change-password-recovery.interface';
import { PoPageChangePasswordRequirement } from './interfaces/po-page-change-password-requirement.interface';

/**
 * @description
 *
 * O componente `po-page-change-password`  utilizado como template para tela de cadastro ou alterao de senha.
 *
 * Apresenta dicas e regras para senhas mais seguras e tambm possibilidade de personalizar o redirecionamento para as telas
 * 'esqueceu a senha', 'voltar' e 'entrar no sistema'. Os textos das telas so pr-definidos e imutveis.
 *
 * A propriedade `p-url-new-password` automatiza a rotina do template e simplifica o processo de cadastro/alterao de senha, bastando
 * definir uma url para POST das informaes digitadas pelo usurio.  A flexibilidade e praticidade podem chegar a um nvel em que o
 * desenvolvimento da aplicao no *client side*  desprovida de qualquer cdigo-fonte relacionado  rotina de cadastro/alterao de senha.
 * Seu detalhamento para uso pode ser visto logo abaixo em *propriedades*.
 * Caso julgue necessrio, pode-se tambm definir manualmente a rotina do componente.
 *
 * Para que as imagens sejam exibidas corretamente,  necessrio incluir o caminho delas ao projeto. Para isso, edite
 * o *assets* no arquivo **angular.json** da aplicao na seguinte ordem:
 * ```
 *   "assets": [
 *     "src/assets",
 *     "src/favicon.ico",
 *     {
 *       "glob": "**\/*",
 *       "input": "node_modules/@po-ui/style/images",
 *       "output": "assets/images"
 *     }
 *   ]
 * ```
 */
@Directive()
export abstract class PoPageChangePasswordBaseComponent {
  private _hideCurrentPassword: boolean = false;
  private _recovery: string | PoPageChangePasswordRecovery | Function;
  private _requirements: Array<PoPageChangePasswordRequirement> = [];
  private _urlHome: string = '/';
  protected validatorChange: any;

  confirmPassword: string;
  currentPassword: string;
  modalAction: PoModalAction;
  newPassword: string;
  recoveryUrlType: string;
  showRequirements: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Esconde o campo `Senha atual` para que o template seja para criao de senha.
   *
   * @default `false`
   */
  @Input('p-hide-current-password') set hideCurrentPassword(value: boolean) {
    this._hideCurrentPassword = convertToBoolean(value);
  }

  get hideCurrentPassword(): boolean {
    return this._hideCurrentPassword;
  }

  /**
   * @optional
   *
   * @description
   *
   * Caminho para a logomarca localizada na parte superior.
   *
   * > Caso seja indefinida o espao se mantm preservado porm vazio.
   */
  @Input('p-logo') logo?: string;

  /**
   * @optional
   *
   * @description
   *
   * URL para a ao do link `Esqueceu a senha`.
   *
   * A propriedade aceita os seguintes tipos:
   *
   * - **String**: informe uma url externa ou uma rota vlida;
   * - **Function**: pode-se customizar a ao. Para esta possilidade basta atribuir:
   * ```
   * <po-page-change-password>
   *      [recovery]="this.myFunc.bind(this)";
   * </po-page-change-password>
   * ```
   *
   * - **PoPageChangePasswordRecovery**: cria-se vnculo automtico com o template **po-modal-password-recovery**.
   *   O objeto deve conter a **url** para requisio dos recursos e pode-se definir o **tipo** de modal para recuperao de senha,
   *   **email** para contato e **mscara** do campo de telefone.
   *
   * > Caso no tenha valor o link `Esqueceu a senha` desaparece.
   */
  @Input('p-recovery') set recovery(value: string | Function | PoPageChangePasswordRecovery) {
    this._recovery = value;

    if (isTypeof(value, 'string')) {
      this.recoveryUrlType = isExternalLink(value) ? 'externalLink' : 'internalLink';
    }
  }

  get recovery() {
    return this._recovery;
  }

  /**
   * @optional
   *
   * @description
   *
   * Lista de regras para criao e alterao de senha.
   */
  @Input('p-requirements') set requirements(value: Array<PoPageChangePasswordRequirement>) {
    this._requirements = value || [];
    this.showRequirements = this._requirements.length > 0;
  }
  get requirements() {
    return this._requirements;
  }

  /**
   * @optional
   *
   * @description
   *
   * Caminho para a logomarca localizada no rodap.
   */
  @Input('p-secondary-logo') secondaryLogo?: string;

  /**
   * Token para solicitao de troca/recuperao de senha.
   *
   * > Esta propriedade ser ignorada caso exista um token como parmetro na URL inicial do template.
   */
  @Input('p-token') token?: string;

  /**
   * @optional
   *
   * @description
   *
   * URL para a ao de retorno da pgina.
   *
   * > O boto `Voltar` aparece apenas para telas de alterao de senha, ou seja, s aparece se a propriedade `p-hide-current-password` for
   * falsa.
   *
   * @default `/`
   */
  @Input('p-url-back') urlBack: string = '/';

  /**
   * Endpoint usado pelo template para realizar um POST. Quando preenchido, o mtodo `p-submit` ser ignorado e o componente adquirir
   * automatizao para o processo de cadastro/troca de senha.
   *
   * ### Processo
   * Ao digitar um valor vlido nos campos de senha e pressionar **salvar**,
   * o componente far uma requisio `POST` na url especificada nesta propriedade passando o objeto contendo os valores definidos pelo
   * usurio.
   *
   * ```
   * body {
   *  token?: token,
   *  oldPassword?: oldPassword,
   *  newPassword: newPassword
   * }
   * ```
   *
   * O cdigo de resposta HTTP de status esperado  `204`.
   *
   * Em caso de **sucesso**, ser exibida a modal de confirmao de senha alterada.
   *
   * > O token ser informado pela propriedade `p-token`do componente ou por um *query parameter* na URL do template.
   *
   * *Processo finalizado.*
   *
   * _______________
   *
   * #### Praticidade
   * As informaes do servio de autenticao tambm podem ser transmitidas diretamente pelas configuraos de rota e, desta maneira,
   * dispensa-se qualquer meno e/ou importao do componente `po-page-change-password` no restante da aplicao. O exemplo abaixo
   * exemplifica a forma dinmica com a qual o template de tela de troca de senha pode ser gerado ao navegar para rota `/change-password`, e
   * tambm como ele se comunica com o servio para efetuao do processo de troca de senha do usurio e solicitao de nova senha.
   * Basta definir nas configuraes de rota:
   *
   *
   * ```
   *   import { PoModalPasswordRecoveryType, PoPageChangePasswordComponent } from '@po-ui/ng-templates';
   *
   *   ...
   *   const routes: Routes = [
   *     {
   *       path: 'change-password', component: PoPageChangePasswordComponent, data: {
   *         serviceApi: 'https://po-ui.io/sample/api/new-password',
   *         recovery: {
   *           url: 'https://po-ui.io/sample/api/users',
   *           type: PoModalPasswordRecoveryType.All,
   *           contactMail: 'dev.po@po-ui.com',
   *           phoneMask: '9-999-999-9999'
   *         }
   *       }
   *     }
   *     ...
   *   ];
   *
   *   @NgModule({
   *     imports: [RouterModule.forRoot(routes)],
   *     exports: [RouterModule]
   *   })
   *   export class AppRoutingModule { }
   * ```
   *
   *
   * O metadado `serviceApi` deve ser a **url** para requisio dos recursos de troca de senha. E `recovery`  a interface
   * `PoPageChangePasswordRecovery` responsvel pelas especificaes contidas na modal de recuperao de senha.
   *
   * >  essencial que siga a nomenclatura dos atributos exemplificados acima para sua efetiva funcionalidade.
   *
   */
  @Input('p-url-new-password') urlNewPassword?: string;

  /**
   * @optional
   *
   * @description
   *
   * URL para a ao do boto `Entrar no sistema` da modal de confirmao que aparece aps salvar a senha ou se chamada pelo mtodo
   * `openConfirmation`.
   *
   * @default `/`
   */
  @Input('p-url-home') set urlHome(value: string) {
    this._urlHome = value;
    this.modalAction.action = this.navigateTo.bind(this, this.urlHome);
  }
  get urlHome() {
    return this._urlHome;
  }

  /**
   * @optional
   *
   * @description
   *
   * Funo executada ao submeter o form pelo boto salvar.
   *
   * Caso definida essa funo, a modal de confirmao no aparece, mas pode ser chamada pelo
   * mtodo `openConfirmation`. Exemplo:
   *
   * ```
   * @ViewChild(PoPageChangePasswordComponent) changePassword: PoPageChangePasswordComponent;
   *
   * onSubmit() {
   *  this.changePassword.openConfirmation();
   * }
   *
   * ```
   * > Esta propriedade ser ignorada se for definido valor para a propriedade `p-url-new-password`.
   */
  @Output('p-submit') submit: EventEmitter<any> = new EventEmitter();

  abstract navigateTo(url: string): void;
}
