import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { browserLanguage, poLocaleDefault } from '../../utils/util';

import { poModalPasswordRecoveryLiterals } from './literals/i18n/po-modal-password-recovery-literals';
import { PoModalPasswordRecoveryType } from './enums/po-modal-password-recovery-type.enum';

const PoModalPasswordRecoveryDefaultMaxLength = 15;
const PoModalPasswordRecoveryDefaultMinLength = 15;
const PoModalPasswordRecoveryDefaultPhone = '(99) 99999-9999';
const PoModalPasswordRecoveryTypeDefault: PoModalPasswordRecoveryType = PoModalPasswordRecoveryType.Email;

/**
 * @description
 *
 * O componente `po-modal-password-recovery`  utilizado como template para solicitao de troca de senha.
 *
 *  composto por uma modal que possui trs telas, cada uma com as seguintes caractersticas:
 *
 * - A primeira possui campos para preenchimento de email ou nmero de telefone;
 * - Tela com campo para preenchimento de cdigo SMS enviado para o nmero de telefone enviado;
 * - A terceira se trata de uma confirmao de envio de link para a caixa de email do usurio.
 *
 *
 * A propriedade `p-url-recovery` automatiza a rotina do componente e simplifica o processo
 * para recuperao de senha, bastando definir uma url para requisio dos recursos.
 * Seu detalhamento para uso pode ser visto logo abaixo em *propriedades*.
 * Caso julgue necessrio, pode-se tambm definir manualmente a rotina do componente.
 *
 *
 * Para a modal de digitao de cdigo SMS,  possvel definir uma mensagem de erro
 * customizada com a propriedade `p-code-error` e h um link para
 * reenvio de cdigo por SMS. Ao reenviar, o evento `p-code-submit` envia um objeto com o telefone do usurio e a quantidade
 * de vezes em que o usurio fez a solicitao de reenvio.
 *
 * >  indicada a utilizao da tela de digitao para envio de cdigo SMS apenas
 * se a opo por envio SMS for disponibilizada para o usurio.
 *
 *
 * A modal de confirmao contm uma ao de reenvio e o evento `p-submit`
 *  quem passa o objeto contendo o email em conjunto com a quantidade de tentativas de reenvio.
 *
 * > A tela de confirmao  indicada para quando o usurio solicitar a troca atravs do email.
 *
 * > Os textos das modals so pr-definidos, imutveis e so traduzidos de acordo com o idioma do *browser* (pt, en e es)
 *
 * Para que as imagens sejam exibidas corretamente,  necessrio incluir o caminho delas ao projeto. Para isso, edite
 * o *assets* no arquivo **angular.json** da aplicao na seguinte ordem:
 * ```
 *   "assets": [
 *     "src/assets",
 *     "src/favicon.ico",
 *     {
 *       "glob": "**\/*",
 *       "input": "node_modules/@po-ui/style/images",
 *       "output": "assets/images"
 *     }
 *   ]
 * ```
 */
@Directive()
export abstract class PoModalPasswordRecoveryBaseComponent {
  private _contactEmail: string;
  private _phoneMask = PoModalPasswordRecoveryDefaultPhone;
  private _type: PoModalPasswordRecoveryType = PoModalPasswordRecoveryTypeDefault;

  email: string;
  maxLength = PoModalPasswordRecoveryDefaultMaxLength;
  minLength = PoModalPasswordRecoveryDefaultMinLength;
  modalPasswordRecoveryTypeAll: boolean;
  phone: string;
  smsCode: string;
  smsCodeErrorMessage: string;

  literals: {
    cancelButton: string;
    closeButton: string;
    continueButton: string;
    email: string;
    emailErrorMessagePhrase: string;
    emailSentConfirmationPhrase: string;
    emailSentTitle: string;
    forgotPasswordTitle: string;
    insertCode: string;
    insertEmail: string;
    insertPhone: string;
    phoneErrorMessagePhrase: string;
    prepositionIn: string;
    prepositionOr: string;
    recoveryPasswordPhrase: string;
    resendEmailButton: string;
    resendSmsCodePhrase: string;
    sendAgain: string;
    sendAgainPhrase: string;
    sendButton: string;
    sms: string;
    smsCode: string;
    smsCodeErrorMessagePhrase: string;
    sentSmsCodePhrase: string;
    supportContact: string;
    telephone: string;
    typeCodeTitle: string;
  } = {
    ...poModalPasswordRecoveryLiterals[poLocaleDefault],
    ...poModalPasswordRecoveryLiterals[browserLanguage()]
  };

  /**
   * @optional
   *
   * @description
   *
   * Definio do e-mail que  exibido na mensagem para contato de suporte.
   */
  @Input('p-contact-email') set contactEmail(value: string) {
    this._contactEmail = value;

    this.smsCodeErrorMessage = this.concatenateSMSErrorMessage(value);
  }

  get contactEmail() {
    return this._contactEmail;
  }

  /**
   * @optional
   *
   * @description
   *
   * Definio de mensagem de erro customizada para quando o usurio passar um cdigo SMS invlido ou errado.
   */
  @Input('p-code-error') codeError: string;

  /**
   * @optional
   *
   * @description
   *
   * Definio da mascara do campo de telefone.
   *
   * @default `(99) 99999-9999`
   */
  @Input('p-phone-mask') set phoneMask(value: string) {
    this._phoneMask = value || PoModalPasswordRecoveryDefaultPhone;
    this.minLength = this.maxLength = this._phoneMask.length;
  }

  get phoneMask() {
    return this._phoneMask;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tipo de recuperao de senha que ser exibido.
   *
   * @default `PoModalPasswordRecoveryType.Email`
   *
   */
  @Input('p-type') set type(value: PoModalPasswordRecoveryType) {
    this._type = (<any>Object).values(PoModalPasswordRecoveryType).includes(value)
      ? value
      : PoModalPasswordRecoveryTypeDefault;
  }

  get type() {
    return this._type;
  }

  /**
   * @optional
   *
   * @description
   *
   * Endpoint usado pelo template para requisio do recurso. Quando preenchido,
   * o mtodos `p-submit` e `p-submit-code` sero ignorados e o componente adquirir automatizao
   * para o processo de solicitao de troca de senha.
   *
   * ### Processos
   * Ao digitar um valor vlido no campo de email/telefone e pressionar **enviar**,
   * o componente far uma requisio `POST` na url especificada nesta propriedade passando o objeto contendo o valor definido pelo usurio.
   *
   * ```
   * body {
   *  email: email,
   *  retry?: retry
   * }
   * ```
   *
   *
   * #### Recuperao por email
   * Para a recuperao de senha por **email**, o cdigo de resposta HTTP de status esperado  `204`.
   *
   * Em caso de **sucesso**, ser exibida a modal de confirmao de e-mail para o usurio.
   *
   *
   * > A ao **Reenviar** na tela de confirmao efetua uma nova requisio
   * passando-se o objeto com incremento para o valor da propriedade **retry**.
   *
   * *Processo finalizado.*
   *
   *
   * #### Recuperao por SMS
   * Se a opo de recuperao for por **SMS**, o cdigo de status de sucesso deve ser `200`.
   * Em caso de **sucesso**, abre-se a modal de digitao de cdigo SMS e a resposta
   * desta requisio deve retornar uma definio de dados abaixo:
   *
   * ```
   * 200:
   *  {
   *    hash: hash,
   *    urlValidationCode?: url
   *  }
   * ```
   *
   *
   * - O **hash** ser o cdigo de validao da solicitao do SMS para ser enviado juntamente com o cdigo de verificao do SMS;
   * - **urlValidationCode**  a url usada para validao do cdigo enviado por SMS.
   *
   *
   * > Caso no seja passado urlValidationCode, o endpoint usado para validao do cdigo ser `<p-url-recovery>/validation`.
   *
   *
   * #### Validao do cdigo SMS
   * Ao digitar um valor vlido no campo de cdigo SMS e pressionar **continuar**, o componente far uma requisio `POST` contendo:
   *
   * ```
   * POST /<p-url-recovery>/validation OU /<urlValidationCode>
   * Body {
   *  hash: hash,
   *  code: code
   * }
   * ```
   *
   *
   * O cdigo de resposta HTTP de status esperado  `200`.
   *
   * Em caso de **erro** na validao do cdigo SMS, a modal se mantm com o campo para digitao
   * de cdigo SMS
   *
   *
   * > Pode-se atribuir a mensagem de erro (message) para o atributo `p-code-error` conforme retorno abaixo:
   *
   * ```
   * 400
   *  {
   *    error {
   *      message: 'Error Message'
   *    }
   *  }
   * ```
   *
   *
   * Em caso de **sucesso**, espera-se a resposta desta requisio retornando a seguinte definio:
   *
   * ```
   * 200:
   *  {
   *    token: token,
   *    urlChangePassword?: url
   *  }
   * ```
   *
   *
   * - **token**: Token de alterao de senha;
   * - **urlChangePassword**: url para o formulrio de alterao de senha.
   *
   *
   * O componente est configurado para redirecionar para a url estabelecida em `urlChangePassword`.
   *
   * > Caso no seja passado valor para urlChangePassword,
   * a url usada para validao ser a `<p-url-recovery>/changePassword?token=<token>`.
   *
   * *Processo finalizado.*
   */
  @Input('p-url-recovery') urlRecovery?: string;

  /**
   * @optional
   *
   * @description
   *
   * Ao contendo como parmetro o cdigo enviado por SMS e digitado pelo usurio.
   *
   * > Esta propriedade ser ignorada se for definido valor para a propriedade `p-url-recovery`.
   */
  @Output('p-code-submit') codeSubmit = new EventEmitter<any>();

  /**
   * @optional
   *
   * @description
   *
   * Ao contendo o email como parmetro e que  executada quando o usurio clica sobres os botes de 'enviar' e 'reenviar' e-mail.
   *
   * > Esta propriedade ser ignorada se for definido valor para a propriedade `p-url-recovery`.
   */
  @Output('p-submit') submit = new EventEmitter<any>();

  /**
   * Aco para concluso de processo e fechamento da modal. Indica-se sua utilizao
   * para aps o envio e validao do cdigo SMS enviado pelo usurio.
   *
   * > Nas modals em que h a ao de 'cancelar' dispensa-se o uso desta ao pois o componente j trata o fechamento da modal.
   */
  abstract completed(): void;

  /**
   * Abre a modal de preenchimento de email ou nmero de telefone para solicitao de troca de senha.
   */
  abstract open(): void;

  /**
   * Abre a modal de confirmao de envio de email.
   */
  abstract openConfirmation(): void;

  /**
   * Abre a modal de preenchimento do cdigo SMS enviado ao usurio.
   */
  abstract openSmsCode(): void;

  private concatenateSMSErrorMessage(value: string) {
    const literalCodeErrorMessage = this.literals.smsCodeErrorMessagePhrase;

    return value && value !== ''
      ? `${literalCodeErrorMessage} ${this.literals.prepositionIn} ${value}.`
      : literalCodeErrorMessage;
  }
}
